/* General Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1c1c1c; /* Dark background for the whole app */
  color: #e0e0e0; /* Lighter text for better contrast */
}

/* Main Layout */
.App {
  text-align: left;
  background-color: #1c1c1c; /* Dark background */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Navbar Styles */
.navbar {
  background-color: #333; /* Dark navbar background */
  color: #f0db4f; /* Navbar text color */
  padding: 10px 0;
  text-align: center;
}

/* Content Styles */
.content {
  flex: 1;
  padding: 20px;
  background-color: #333; /* Slightly lighter background for content */
  border-radius: 8px;
  margin: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
}

/* Footer Styles */
.footer {
  background-color: #333; /* Slightly lighter than the main background */
  color: white;
  text-align: center;
  padding: 20px 0;
}

/* Links Styles */
a {
  color: #f0db4f; /* Accent color for links */
  text-decoration: none;
  transition: color 0.3s ease; /* Smooth transition for hover effect */
}

a:hover {
  color: #e0e0e0; /* Lighter color on hover */
  text-decoration: none;
}

/* Blog Post Styles */
.blog-post {
  background-color: #2a2a2a; /* Background for individual blog posts */
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  text-align: left;
}

.blog-post img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-bottom: 1px solid #333;
}

.blog-post .content {
  padding: 15px;
}

.blog-post h3 {
  margin-top: 0;
  color: #f0db4f;
}

.blog-post p {
  color: #e0e0e0;
}

.blog-post a {
  display: inline-block;
  margin-top: 10px;
  color: #28a745;
  text-decoration: none;
  transition: color 0.3s ease;
}

.blog-post a:hover {
  color: #e0e0e0;
}
